/* General Reset */
*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Layout for the Dashboard */
.dashboard-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px;
}

/* Chart Layout */
.chart-container {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  width: 100%;
}

/* Labels */
.labels-container {
  width: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.label-item {
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  white-space: nowrap;
}

/* Bar Chart Wrapper */
.bar-chart-wrapper {
  flex: 1;
  min-width: 0;
}

/* Tooltip */
.recharts-tooltip-wrapper {
  pointer-events: auto;
}

.tooltip {
  display: block;
  position: absolute;
  z-index: 1000;
  pointer-events: none;
}

/* Tabs Styling */
.tabs-list {
  display: flex;
  gap: 16px;
}

.tab-trigger {
  padding: 8px 16px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
  cursor: pointer;
}

.tab-trigger[data-state='active'] {
  background-color: #2563eb;
  color: white;
}

/* Select Dropdown */
.select-trigger {
  display: inline-block;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 8px;
  cursor: pointer;
}
